<template>

    <div class="container">
<!--        <button class="child btn" id="left" @click="decrement(1)"></button>-->
        <div class="arrowL" @click="decrement(1)"><span></span></div>
        <div class="date">{{monthGet}}</div>
<!--        <button class="child btn right" id="right" @click="increment(1)">Next >>></button>-->
        <div class="arrowR" @click="increment(1)"><span></span></div>
    </div>
</template>

<script>
import {mapMutations}        from 'vuex';
import {mapGetters}        from 'vuex';
import * as types          from '../store/types';


    export default {
        data() {
            return{
                monthName: new Date().toString().split(' ')[1]
            }
        },

        computed:{
            ...mapGetters({
                counter: types.COUNTER
            }),
            monthGet: function() {
                let getter = new Date(new Date().getFullYear(),new Date().getMonth()+this.counter).toString().split(' ');
                return `This is ${getter[1]}, ${getter[3]}`;
            },

        },

		methods: {
			...mapMutations({
                increment: types.MUTATE_COUNTER_INCREMENT,
                decrement: types.MUTATE_COUNTER_DECREMENT
            }),


		},
		actions: {},
		components: {},



    }
</script>

